@import url("https://fonts.googleapis.com/css2?family=Bowlby+One+SC&family=DM+Serif+Text&family=Fredoka+One&family=Inter:wght@100;200;300;400&family=Jost:wght@400;500&family=Poiret+One&family=Raleway:wght@400;500;600;700;900&display=swap");
body.ocultar {
  overflow: hidden;
}

.portada {
  width: 100%;
  height: 580px;
  background: url(https://images.pexels.com/photos/1639562/pexels-photo-1639562.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=550&w=1280);
  background-size: cover;
  background-position: center;
}
.portada .header-navegacion {
  position: relative;
  justify-content: space-evenly;
}
.portada .header-navegacion .navegacion-nombre .title-1 {
  color: #fff;
  font-family: "Fredoka One", cursive;
  font-size: 35px;
  animation: titulo1 3s ease infinite;
}
.portada .header-navegacion .navegacion-nombre .title-2 {
  color: #f36412;
  font-family: "Fredoka One", cursive;
  font-size: 35px;
  animation: titulo2 3s ease infinite;
}
.portada .header-navegacion .navegacion-links a {
  text-decoration: none;
  color: #fff;
  font-family: "Raleway", sans-serif;
  padding: 0 10px;
  font-weight: 600;
  transition: 0.3s ease-in;
}
.portada .header-navegacion .navegacion-links a:hover {
  color: #f36412;
}
.portada .header-navegacion .navegacion-links .resumen-icon .menu-resumen {
  color: #fff;
  font-size: 30px;
  margin-right: 10px;
}
.portada .header-navegacion .navegacion-links .resumen-icon .menu-resumen:hover {
  color: #fff;
}
.portada .header-navegacion .navegacion-links .resumen-icon .resumen-contador {
  top: -5px;
  right: -10px;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background: #f12711;
  display: flex;
  justify-content: center;
  align-items: center;
}
.portada .header-navegacion .navegacion-links .resumen-icon .resumen-contador p {
  color: #fff;
  font-family: "Raleway", sans-serif;
}
.portada .descripcion {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.portada .descripcion p {
  color: #fff;
  font-size: 36px;
  font-family: "Raleway", sans-serif;
  font-weight: 600;
}
.portada .descripcion .btn-menu {
  font-family: "Raleway", sans-serif;
  font-weight: bold;
  color: #212121;
  padding: 12px 10px;
}

.menu {
  height: auto;
}
.menu .title-menu {
  font-family: "Fredoka One", cursive;
  font-size: 45px;
  padding: 40px 170px;
}
.menu .title-menu::after {
  position: absolute;
  display: block;
  content: "";
  width: 10%;
  height: 6px;
  background: #f12711;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #f5af19, #f12711);
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #f5af19, #f12711);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  border-radius: 10px;
  animation: linea 3s ease infinite;
}
.menu .ctr-menu-1 {
  flex-wrap: wrap;
  position: relative;
  width: 78%;
  margin: auto;
  height: auto;
  display: none;
  justify-content: center;
}
.menu .ctr-menu-1 .card-menu {
  box-shadow: 4px 5px 8px #f6f2f2;
  width: 315px;
  height: 500px;
  margin: 12px;
  transition: 0.8s ease transform;
}
.menu .ctr-menu-1 .card-menu .menu-img {
  height: 50%;
}
.menu .ctr-menu-1 .card-menu .menu-img img {
  width: 100%;
  height: 100%;
}
.menu .ctr-menu-1 .card-menu .menu-descripcion h1 {
  font-size: 28px;
}
.menu .ctr-menu-1 .card-menu .menu-descripcion .agregar-pedido {
  width: 88%;
  margin: auto;
  display: block;
  background: #f5af19;
  color: #212121;
  padding: 7px 0;
  border: none;
  font-size: 17px;
}
.menu .ctr-menu-1 .card-menu:hover {
  transform: translateY(-10px);
  box-shadow: 4px 8px 8px #f6f2f2;
}
.menu .ctr-menu-1.activo {
  display: flex;
}
.menu .ctr-menu-2 {
  flex-wrap: wrap;
