.menu .controles {
  position: absolute;
  top: 50%;
  color: #f36412;
}
.menu .izquierda {
  left: 100px;
}
.menu .izquierda .atras {
  font-size: 48px;
  cursor: pointer;
  animation: botones 2s ease-in infinite;
}
.menu .izquierda .atras.ocultar {
  display: none;
}
.menu .izquierda.ocultar {
  display: none;
}
.menu .derecha {
  right: 100px;
}
.menu .derecha .siguiente {
  font-size: 48px;
  cursor: pointer;
  animation: botones 2s ease-in infinite;
}
.menu .derecha.ocultar {
  display: none;
}

.registro {
  width: 100%;
  height: 550px;
  background: url(https://cdn.pixabay.com/photo/2016/11/18/14/05/brick-wall-1834784_960_720.jpg);
  background-size: cover;
  background-position: center;
  position: relative;
}
.registro .title {
  color: #f5af19;
}
.registro .importante {
  color: #fff;
  position: relative;
}
.registro::before {
  position: absolute;
  top: 0;
  width: 100%;
  height: 550px;
  content: "";
  background: linear-gradient(#212121 5%, transparent);
}

#formulario {
  width: 63%;
  margin: auto;
}
#formulario .container-errors {
  background: #f36412;
  text-align: center;
  padding: 20px 0;
  font-family: "Raleway", sans-serif;
  color: #fff;
  font-weight: bold;
  display: none;
}
#formulario .container-errors.error {
  display: block;
}

.portada::before {
  position: absolute;
  top: 0;
  width: 100%;
  height: 550px;
  content: "";
  background: linear-gradient(#212121 10%, transparent);
}

.footer {
  background: #212121;
  height: 80px;
}
.footer .navegacion-footer a {
  margin: 0 10px;
  color: #fff;
  font-family: "Raleway", sans-serif;
  font-weight: 500;
}
.footer .navegacion-footer a:hover {
  color: #f5af19;
}
.footer p {
  font-size: 25px;
}

.precio {
  width: 50px;
  height: 50px;
  background: #f12711;
  top: 0;
  right: 0;
}
.precio p {
  color: #fff;
  margin: 0;
  font-family: "Raleway", sans-serif;
  font-weight: 800;
}

.modal-resumen {
  background: #212121;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  clip-path: circle(0 at center);
  justify-content: center;
  align-items: center;
  transition: 0.7s ease clip-path;
}
.modal-resumen .modal-alerta {
  width: 550px;
  height: 350px;
  margin-bottom: 100px;
}
.modal-resumen .modal-alerta img {
  width: 100%;
  height: 100%;
}
.modal-resumen .modal-alerta .alerta-msg {
  color: #fff;
  font-family: "Fredoka One", cursive;
  font-size: 39px;
}
.modal-resumen .modal-alerta .btn-menu {
  background: #f36412;
  border: none;
  padding: 10px 20px;
  font-family: "Raleway", sans-serif;
  font-weight: 600;
}
.modal-resumen.mostrar {
  clip-path: circle(100% at center);
}
.modal-resumen .salir {
  color: #f5af19;
  top: 20px;
  right: 50px;
  font-size: 38px;
  cursor: pointer;
}
.modal-resumen.res-final {
  display: block;
}
.modal-resumen.modal-resumenes {
  display: flex;
}

.resumen-final {
  width: 90%;
  margin: auto;
}
.resumen-final .resumen-ctr {
  height: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 20px;
  height: 500px;
}
.resumen-final .resumen-ctr .resumen-lista {
  grid-column: 1/3;
  height: 100%;
  overflow-y: scroll;
}
.resumen-final .resumen-ctr .resumen-compra {
  grid-column: 3/4;
}
.resumen-final .resumen-ctr .resumen-compra .resumen-envio {
  font-family: "Raleway", sans-serif;
  width: 90%;
  margin: auto;
  border: 1px dashed #fff;
  height: 80px;
}
.resumen-final .resumen-ctr .resumen-compra .resumen-envio .icon-envio {
  color: #f36412;
  font-size: 35px;
}
.resumen-final .resumen-ctr .resumen-compra .resumen-envio p {
  color: #fff;
  font-size: 18px;
}
.resumen-final .resumen-ctr .resumen-compra .resumen-pago {
  width: 90%;
  margin: auto;
  border: 1px dashed #fff;
  height: auto;
}
.resumen-final .resumen-ctr .resumen-compra .resumen-pago .title-resumen {
  color: #f5af19;
  font-size: 20px;
  font-weight: 800;
}
.resumen-final .resumen-ctr .resumen-compra .resumen-pago .resumen-articulos {
  color: #fff;
  font-family: "Raleway", sans-serif;
  font-size: 20px;
}
.resumen-final .resumen-ctr .resumen-compra .resumen-pago .resumen-total {
  color: #fff;
  font-family: "Raleway", sans-serif;
  font-size: 20px;
}
.resumen-final .resumen-ctr .resumen-compra .resumen-pago .boton-compra {
  background: #f36412;
  width: 100%;
  border: none;
}
.resumen-final .resumen-ctr .resumen-compra .resumen-pago .boton-compra:hover {
  background: #f5af19;
}

@keyframes titulo1 {
  0% {
    color: #fff;
  }
  50% {
    color: #f36412;
  }
  100% {
    color: #fff;
  }
}
@keyframes titulo2 {
  0% {
    color: #f36412;
  }
  50% {
    color: #fff;
  }
  100% {
    color: #f36412;
  }
}
@keyframes botones {
  0% {
    transform: scale(1.1);
  }
  50% {
    transform: scale(1);
  }
  100% {
    transform: translateY(1.1);
  }
}
@keyframes linea {
  0% {
    width: 10%;
  }
  50% {
    width: 0%;
  }
  100% {
    width: 10%;
  }
}